<template>
	<div class="receipts">
	<HeadNav></HeadNav>
    <LeftMenu></LeftMenu>
    
    <div class="rightContainer">
    	    
		<div style="width: 60%; margin:30px auto 30px auto;">
			<el-input placeholder="" v-model="input3" class="input-with-select">
				<el-select v-model="select" slot="prepend" placeholder="">
					<el-option label="" value="1"></el-option>
					<el-option label="" value="2"></el-option>
					<el-option label="" value="3"></el-option>
				</el-select>
				<el-button slot="append" icon="el-icon-search"></el-button>
			</el-input>
		</div>
		
		<el-dialog
		  title=""
		  :visible.sync="dialogVisible"
		  width="60%"
		  :before-close="handleClose">
		  
		  
		  	<div class="demo-input-suffix">
			  
			  <el-input v-model="input" placeholder="" style='width:60%'></el-input>
			</div>
			
			
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="dialogVisible = false"> </el-button>
		    <el-button type="primary" @click="dialogVisible = false"> </el-button>
		  </span>
		</el-dialog>

    	
    	<div class="tablebox">

				<el-table :data="tableData" style="width: 100%" height="600">
					<el-table-column prop="Id" label=" id" width="150">
					</el-table-column>
					<el-table-column prop="CreateAt" label="" width="120">
					</el-table-column>
					<el-table-column prop="UpdateAt" label="" width="120">
					</el-table-column>
					<el-table-column prop="DeleteAt" label="" width="120">
					</el-table-column>
					<el-table-column prop="excel_id" label="ID" width="120">
					</el-table-column>
					<el-table-column prop="time" label="" width="120">
					</el-table-column>
					<el-table-column prop="type" label="" width="120">
					</el-table-column>
					<el-table-column prop="status" label="" width="120">
					</el-table-column>
					<el-table-column prop="client" label="" width="120">
					</el-table-column>
					<el-table-column prop="remarks" label="" width="120">
					</el-table-column>
					<el-table-column prop="amount" label="" width="120">
					</el-table-column>
					<el-table-column prop="used" label="" width="120">
					</el-table-column>
					<el-table-column prop="balance" label="" width="120">
					</el-table-column>
					<el-table-column prop="Version" label="" width="120">
					</el-table-column>

					<el-table-column fixed='right' label="" width="300">
						<template slot-scope="scope">
							
						  <el-button  size="mini" @click="dialogVisible = true"></el-button>

						  <el-button size="mini" type="danger" @click="handleDelete(scope.row)"></el-button>
						  <el-button size="mini" type="success" @click='details(scope.row)'></el-button>
						</template>
					</el-table-column>
				</el-table>
				
				<div style="margin-top: 30px;">
					<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[20, 3, 160, 170]" :page-size="20" layout="total, sizes, prev, pager, next, jumper" :total="totle">
					</el-pagination>
				</div>
				
			</div>
      <router-view></router-view>
    </div>
	</div>
</template>

<script>
import HeadNav from "../components/HeadNav";
import LeftMenu from "../components/LeftMenu";
import Qs from 'qs';
export default {
  name: "Order",
  data(){
  	return {
        tableData: [],
        
        input3: '',// 
      	select: '',// 
        currentPage: 1,//
        totle:0,//
        tiao:0,//
        
        dialogVisible: false,
        input:'',
        
        
        
        currentPage: 1,//
        totle:0,//
        tiao:0,//
      	
      }
  },
  components: {
    HeadNav,
    LeftMenu
  },
  methods: {
  	
  		handleClose(done) {
	        this.$confirm('')
	          .then(_ => {
	            done();
	          })
	          .catch(_ => {});
       },

  	
		/*handleEdit(index,row){//

		  	this.suoindex = index;
		  	this.hang.push(row.id);
	
		  	
		  	
		},*/
		
		//
		details(row){
			console.log(row);
			this.$axios.post(`/api/co/info`,Qs.stringify({id:row.Id})).then(res => {
		  		
		  		console.log(res);
	
		  	})
		},
		
		save(index,row){//
			
			this.suoindex = -1;// -1
			
			this.hang = [];
			
		},
		
		youid(id){//
			
			return this.hang.includes(id);
			
		},
		
		handleDelete(row){
			console.log(row.Id);
			this.$axios.post(`/api/sale/del`,Qs.stringify({id:row.Id})).then(res => {
		  		
		  		console.log(res);
	
		  	})
			
		},
		
		handleSizeChange(val) {
		
		//console.log(` ${val} `);
		this.tiao = val;
		this.$axios.post(`/api/sale/search?page=${val}`).then(res => {
	  		
	  		this.tableData = res.data.data.sale;

	  	})
	    
	    
		},
		handleCurrentChange(val) {
		    //console.log(`: ${val}`);
		    this.$axios.post(`/api/sale/search?number=${val}&page=${this.tiao}`).then(res => {
		  		
		  		this.tableData = res.data.data.sale;
	
		  	})
		},
	},
	
  	mounted(){
  	
	  	this.$axios.post("/api/co/l").then(res => {
	  		
	  		this.tableData = res.data.data.comes;
	  		this.totle = res.data.data.total;
	  		
	  		
	  	})

	}
  
}
</script>

<style scoped>
.receipts {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.rightContainer {
  position: relative;
  top: 0;
  left: 180px;
  width: calc(100% - 180px);
  height: calc(100% - 71px);
  overflow: auto;
}

.tablebox{
	
}

.el-select .el-input {
	width: 130px;
}

.input-with-select .el-input-group__prepend {
	background-color: #fff;
}
</style>